#container {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

#title {
  color: #333;
  font-size: 28px;
}

#subtitle {
  color: #666;
  font-size: 16px;
}

#section {
  color: #555;
  font-size: 24px;
  margin-top: 20px;
}

#btn {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin-top: 20px;
  border-radius: 5px;
  text-decoration: none;
}

#btn:hover {
  text-decoration: underline;
}

#table-student {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  color: black; /* Menjadikan teks berwarna hitam */
}

#table-student td {
  padding: 10px;
}

.student-data-row:hover {
  background-color: #f0f0f0;
}

.delete-button {
  padding: 5px 10px;
  background-color: #dc3545;
  border: none;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  border-radius: 3px;
  cursor: pointer;
}

.delete-button:hover {
  background-color: #c82333;
}

.loading {
  text-align: center;
  margin-top: 20px;
  font-style: italic;
  color: #555;
}
